import React, { useEffect, useRef, ReactDOM } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import TypeIt from "typeit-react";

const Home = () => {
  gsap.registerPlugin(ScrollTrigger);
  const ref = useRef(null);

  useEffect(() => {
    const element = ref.current;
    gsap.fromTo(
      element.querySelector("#img"),
      {
        scale: 1.3,
      },
      {
        scale: 0.7,
        duration: 2.5,
        ease: "linear",
        scrollTrigger: {
          trigger: element.querySelector(".mainImg"),
          start: "top center",
          end: "bottom top",
          scrub: true
        }
      }
    );
  }, []);

  useEffect(() => {
    const element = ref.current;
    gsap.fromTo(
      element.querySelector(".b1"),
      {
        x: -800,
      },
      {
        x: 0,
        duration: 2,
        delay: 1,
        ease: "linear",
        scrollTrigger: {
          trigger: element.querySelector("#Art span"),
          scrub: true
        }
      }
    );
  }, []);

  useEffect(() => {
    const element = ref.current;
    gsap.fromTo(
      element.querySelector(".b4"),
      {
        x: 1000
      },
      {
        x: 0,
        duration: 1,
        ease: "linear",
        scrollTrigger: {
          trigger: element.querySelector(".ArtImg"),
          scrub: true
        }
      }
    );
  }, []);







  return (
    <div ref={ref}>
      <div className="mainText">
        <p>
          <TypeIt
            options={{
              strings: ["Grimpan exists for those who cherish their daily lives, dream of a perfect day, and are willing to enjoy art fordevelopment.Where everyone's inspiration for accepting changes and trends in art culture and responding flexibly becomes a reality.It's called Grimpan."],
              speed: 30,
              loop: true,
              waitUntilVisible: false,
            }} />
        </p>
      </div>

      <div className="mainImg">
        <figure >
          <img src="/grimpan/img/main.jpg" alt="main" id="img" />
        </figure>
      </div>
      <section id="About">
        <h2>About</h2>
        <div className="AboutText">
          <p>Grimpan  2021 8  65         ,        .</p>
          <p>                   .     Grimpan      .</p>
          <p>Launched in August 2021, it introduces 65 domestic cultural and artistic creat ors and sells their works, and operates an exhibition-type pop-up store along with an online store.</p>
          <p>Now taking a step forward so that you can enjoy art in your daily life, not just on exhibitions or special occasions. For your artistic lifestyle, Grimpan delivers inspiration throughout your daily life with art products.</p>
        </div>
      </section>
      <section id="Art">
        <h2>the art of life</h2>
        <p>
          <span>
                     .
          </span>
          <span>
                     .  ,   Grimpan .
          </span>
        </p>
        <div className="ArtImg">
          <figure>
            <img src="/grimpan/img/Artimg.jpg" alt="artimg" />
          </figure>
        </div>
        <div className="artLife">
          <h2>Life</h2>
          <div className="artBox">
            <div className="box b1">
              <figure>
                <img src="/grimpan/img/arts1.jpg" alt="arts1" />
              </figure>
            </div>
            <div className="box b2">
              <div className="front">
                <figure>
                  <img src="/grimpan/img/artbig1.jpg" alt="artbig1" />
                </figure>
              </div>
              <div className="back">
                <span>Life</span>
              </div>
            </div>
            <div className="box b3">
              <div className="front">
                <figure>
                  <img src="/grimpan/img/artbig2.jpg" alt="artbig2" />
                </figure>
              </div>
              <div className="back">
                Life
              </div>
            </div>
            <div className="box b4">
              <figure>
                <img src="/grimpan/img/arts2.jpg" alt="arts2" />
              </figure>
            </div>
          </div>
        </div>
      </section>
      <section id="Product">
        <div className="Artist">
          <div className="artist_left">
            <div className="artist_text">
              <h2>Artist</h2>
              <p>
                Grimpan   ,    ,    .
              </p>
            </div>
            <button type="button">VIEW MORE</button>
          </div>
          <div className="artist_right">
            <figure>
              <img src="/grimpan/img/pro1.jpg" alt="pro1" />
            </figure>
          </div>
        </div>
        <div className="Product">
          <div className="product_left">
            <figure>
              <img src="/grimpan/img/pro2.jpg" alt="pro2" />
            </figure>
          </div>
          <div className="product_right">
            <div className="product_text">
              <h2>Product</h2>
              <p>Grimpan        .</p>
            </div>
            <button type="button">VIEW MORE</button>
          </div>
        </div>
      </section>
      <section id="popup">
        <h2>PopUp</h2>
        <div className="swiperB">
          <Swiper
            slidesPerView={1}
            spaceBetween={0}
            pagination={{
              clickable: true,
            }}
            modules={[]}
            breakpoints={{
              450: {
                slidesPerView: 2,
                spaceBetween: 30,
              },
              850: {
                slidesPerView: 3,
                spaceBetween: 30,
              }

            }}
            className="mySwiper"
          >
            <SwiperSlide>
              <div className="s_box">
                <figure>
                  <img src="/grimpan/img/pop1.jpg" alt="pop1" />
                </figure>
                <p> </p>
                <span>03/30 - 5/10</span>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="s_box">
                <figure>
                  <img src="/grimpan/img/pop2.jpg" alt="pop2" />
                </figure>
                <p> </p>
                <span>05/15 - 06/30</span>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="s_box">
                <figure>
                  <img src="/grimpan/img/pop3.jpg" alt="pop3" />
                </figure>
                <p>  </p>
                <span>07/05 - 08/20</span>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="s_box">
                <figure>
                  <img src="/grimpan/img/pop4.jpg" alt="pop4" />
                </figure>
                <p>  </p>
                <span>08/30 -1 0/01</span>
              </div>
            </SwiperSlide>
          </Swiper>

        </div>
      </section>
      <section id="videos">
        <h2>Videos</h2>
        <div className="video_content">
          <div className="vbox">
            <figure>
              <img src="/grimpan/img/v1.jpg" alt="v1" />
              <span></span>
            </figure>
            <h3>   </h3>
            <p>Live painting  </p>

          </div>
          <div className="vbox">
            <figure>
              <img src="/grimpan/img/v2.jpg" alt="v2" />
              <span></span>
            </figure>
            <h3>  </h3>
            <p>Grimpan  </p>

          </div>
          <div className="vbox">
            <figure>
              <img src="/grimpan/img/v3.jpg" alt="v3" />
              <span></span>
            </figure>
            <h3>Grimpan </h3>
            <p>  </p>

          </div>
          <div className="vbox">
            <figure>
              <img src="/grimpan/img/v4.jpg" alt="v4" />
              <span></span>
            </figure>
            <h3>  </h3>
            <p>Grimpan    </p>

          </div>
        </div>
      </section>
    </div>
  )
}



export default Home;